import React, { useEffect, useRef, useState } from "react";
import "../styles/create-post.css";
import { PostProps } from "./view-post";
import { Editor, EditorState, RichUtils } from "draft-js";
import "draft-js/dist/Draft.css";

interface CreatePostProps {
  onFormSubmit: (post: PostProps) => void;
  nextPostId: string;
}

const CreatePost: React.VFC<CreatePostProps> = ({
  onFormSubmit,
  nextPostId,
}) => {
  const titleInput = useRef<HTMLInputElement>(null);
  useEffect(() => titleInput.current?.focus(), []);

  const [editorState, setEditorState] = useState(() =>
    EditorState.createEmpty()
  );

  const handleKeyCommand = (command: any) => {
    const newState = RichUtils.handleKeyCommand(editorState, command);
    if (newState) {
      setEditorState(newState);
      return "handled";
    }
    return "not-handled";
  };

  return (
    <div className="create-post display-width">
      <h1 className="page-title">Create a Post</h1>

      <form
        onSubmit={(ev) => {
          ev.preventDefault();
          const date = new Date();
          const formData = new FormData(ev.currentTarget);
          onFormSubmit({
            title: formData.get("title") as string,
            description: formData.get("description") as string,
            content: formData.get("content") as string,
            created: date.toUTCString(),
            id: nextPostId,
          });
          ev.currentTarget.reset();
        }}
      >
        <Editor
          editorState={editorState}
          onChange={setEditorState}
          handleKeyCommand={handleKeyCommand}
          placeholder="Enter Title"
        />
        <label>Title:</label>
        <input
          ref={titleInput}
          name="title"
          placeholder="Enter Title"
          required
        />
        <label>Description:</label>
        <textarea
          name="description"
          placeholder="Short caption"
          required
        ></textarea>
        <label>Content:</label>
        <textarea
          name="content"
          placeholder="Write a Story"
          required
        ></textarea>
        <button>Save Post</button>
      </form>
    </div>
  );
};

export default CreatePost;
